<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div v-show="isReading" class="btn-block">
        <a download  class="f-small main-c mr5" href="../gy-files/template/学生信息导入模板.xls" >下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload mr15">批量导入</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
        <div @click="$exportDetail('/export/studentAtSchoolExport.do')" class="btn-main mr15">导出</div>
        <div @click="$router.push('/edu-affair/reading/add')" class="btn-main-deepen">添加学生</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
        <table class="table">
        <thead>
        <tr>
          <th width="8%">姓名</th>
          <th width="8%">性别</th>
          <th width="10%">是否有学籍</th>
          <th width="16%">学籍号</th>
          <th width="10%">所在班级</th>
          <th width="12%">班级学号</th>
          <th width="8%">学生性质</th>
          <th width="10%">出生日期</th>
          <th width="10%">籍贯码</th>
          <th width="8%">民族</th>
          <th width="10%">政治面貌</th>
          <th width="10%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>
            <span @click="goToDetail(item)" class="oper">{{item.name}}</span>
          </td>
          <td>{{$enums.Sex.getName(item.sex)}}</td>
          <td>{{$enums.YesNoStatus.getName(item.isSchoolRoll) || '--'}}</td>
          <td>{{item.flowStudentNo}}</td>
          <td>{{item.aliasName || '--'}}</td>
          <td>{{item.classStudentNo || '--'}}</td>
          <td>
            <span>{{$enums.StudentNature.getName(item.studentNature)}}</span>
            <!--<span v-show="item.studentStatus">/{{$enums.StudentStatus.getName(item.studentStatus)}}</span>-->
          </td>
          <td>{{item.birthday | substrTime}}</td>
          <td>{{item.nativePlace || '--'}}</td>
          <td>{{item.nationName}}</td>
          <td>{{$enums.StudentPoliticsStatus.getName(item.politicsStatus)}}</td>
          <td>
            <span @click="goToDetail(item)" class="oper">详情</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    data() {
      let isReading = this.$route.path === '/edu-affair/reading'
      return {
        // 是否为在读生
        isReading,
        nav: [{name: isReading? "在读生信息" : "学生信息", path: ""}],
        searchData: [
          {type: 'select',title: '性别', model: 'sex', option: this.$enums.Sex.list},
          {type: 'select',title: '民族', model: 'nationId', option: []},
          {type: 'select',title: '是否有学籍', model: 'isSchoolRoll', option: this.$enums.YesNoStatus.list},
          {type: 'select',title: '班级', model: 'classId', option: []},
          {type: 'cascader',title: '学生性质', width: '160px', modelName: ['studentNature', 'studentStatus'], option: []},
          {type: 'select',title: '政治面貌', model: 'politicsStatus', option: this.$enums.StudentPoliticsStatus.list},
          {type: 'input', placeholder: '姓名/学籍号/学号'}
        ],
        table:{
          url: '/student/queryStudentPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            isSchoolRoll: null,
            sex: null,
            classId: null,
            nationId: null,
            studentStatus: null,
            politicsStatus: null,
            keywords: null
          }
        }
      }
    },
    methods: {
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/student/importStudentList.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 获取所有民族
      async getNationList(){
        let res = await this.$ajax({
          url: '/common/queryNationList.do'
        })
        this.searchData[1].option = []
        res.forEach(e=>{
          this.searchData[1].option.push({
            value: e.id,
            label: e.nation
          })
        })
      },
      // 前往学生详情
      goToDetail(item){
        let path = ''
        if(this.isReading){
          path = '/edu-affair/reading/detail'
        }else{
          path = '/student-work/student/detail'
        }
        this.$router.push({
          path,
          query:{
            id: item.id,
            type: item.studentNature
          }
        })
      },
      // 获取所有班级
      async getClassList(){
        let res = await this.$ajax({
          url: '/class/queryClassPage.do',
          query:{
            classStatus: 'UN_GRADUATED',
            pageSize: 100000,
            pageNo: 1
          }
        })
        this.searchData[3].option = res.list.map(e=>{
          return {label: e.aliasName,value: e.classId}
        })
      },
      // 获取学生性质
      async getStudentNature(){
        this.searchData[4].option = await this.$ajax({
          url: '/student/queryStudentNatureList.do'
        })
      }
    },
    mounted(){
      this.$search(this.table)
      this.getNationList()
      this.getClassList()
      this.getStudentNature()
    }
  }
</script>
<style lang="sass" scoped>
  .btn-block
    .main-c
      vertical-align: top
      display: inline-block
      line-height: 30px
      margin-top: 2px
</style>
